<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 警告区域 -->
      <el-alert title="注意: 只允许为第三级分类设置相关参数!" type="warning" show-icon :closable="false">
      </el-alert>

      <!-- 选择商品分类区域 -->
      <el-row class="cat_opt">
        <el-col>
          <span>选择商品分类: </span>
          <!-- 选择商品分类的级联选择框 -->
          <el-cascader v-model="selectCateKeys" :options="catelist" :props="cateProps" @change="handleChange"></el-cascader>
        </el-col>
      </el-row>

      <!-- tab 页签区域 -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <!-- 添加动态参数的面板 -->
        <el-tab-pane label="动态参数" name="many">
          <!-- 添加参数的按钮 -->
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible=true">添加参数</el-button>
          <!-- 动态参数表格 -->
          <el-table :data="manyTableData" border stripe>
            <!-- 展开行 -->
            <el-table-column type="expand">
              <template v-slot="{row}">
                <!-- 循环渲染tag标签 -->
                <el-tag v-for="(item,i) in row.attr_vals" :key="i" closable @close="handleClose(i,row)">{{item}}</el-tag>
                <!-- 输入文本框 -->
                <el-input class="input-new-tag" v-if="row.inputVisible" v-model="row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(row)" @blur="handleInputConfirm(row)">
                </el-input>
                <!-- 添加按钮 -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!-- 索引列 -->
            <el-table-column label="#" type="index"></el-table-column>
            <el-table-column label="参数名称" prop="attr_name"></el-table-column>
            <el-table-column label="操作">
              <template v-slot="{row}">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(row.attr_id)">编辑</el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteParams(row.attr_id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>

        <!-- 添加静态属性的面板 -->
        <el-tab-pane label="静态属性" name="only">
          <!-- 添加属性的按钮 -->
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible=true">添加属性</el-button>
          <!-- 静态属性表格 -->
          <el-table :data="onlyTableData" border stripe>
            <!-- 展开行 -->
            <el-table-column type="expand">
              <template v-slot="{row}">
                <!-- 循环渲染tag标签 -->
                <el-tag v-for="(item,i) in row.attr_vals" :key="i" closable @close="handleClose(i,row)">{{item}}</el-tag>
                <!-- 输入文本框 -->
                <el-input class="input-new-tag" v-if="row.inputVisible" v-model="row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(row)" @blur="handleInputConfirm(row)">
                </el-input>
                <!-- 添加按钮 -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!-- 索引列 -->
            <el-table-column label="#" type="index"></el-table-column>
            <el-table-column label="属性名称" prop="attr_name"></el-table-column>
            <el-table-column label="操作">
              <template v-slot="{row}">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(row.attr_id)">编辑</el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteParams(row.attr_id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!-- 添加参数的对话框 -->
    <el-dialog :title="'添加'+titleText" :visible.sync="addDialogVisible" width="50%" @close="addFormDialogColsed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" status-icon>
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addParams">添 加</el-button>
      </span>
    </el-dialog>

    <!-- 修改参数的对话框 -->
    <el-dialog :title="'修改'+titleText" :visible.sync="editDialogVisible" width="50%" @close="editFormDialogColsed">
      <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="100px" status-icon>
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editParams">提 交</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
    data () {
        return {
            // 商品分类存储数组
            catelist: [],
            // 级联选择框配置对象
            cateProps: {
                expandTrigger: 'hover',
                label: 'cat_name',
                value: 'cat_id',
                children: 'children'
            },
            // 级联选择框双向绑定的数组
            selectCateKeys: [],
            // 选中的标签页名称
            activeName: 'many',
            // 动态参数表格数据
            manyTableData: [],
            // 静态属性表格数据
            onlyTableData: [],
            // 显示 / 隐藏 添加对话框
            addDialogVisible: false,
            // 显示 / 隐藏 修改对话框
            editDialogVisible: false,
            // 添加表单的数据对象
            addForm: {
                attr_name: ''
            },
            // 修改表单的数据对象
            editForm: {},
            // 添加表单的验证规则
            addFormRules: {
                attr_name: [
                    { required: true, message: '请输入参数名称', trigger: 'blur' }
                ]
            }
        }
    },
    created () {
    //  获取所有的商品分类
        this.getCateList()
    },
    methods: {
    //  获取所有的商品分类
        async getCateList () {
            // 向服务器发送请求 获取所有商品分类
            const res = await this.$http.get('categories')
            if (res.meta.status !== 200) {
                return this.$message.error('获取商品分类信息失败!')
            }
            this.catelist = res.data
        },
        // 级联选择框选项变化 触发函数
        async handleChange () {
            // 如果没有选择三级分类数据
            if (this.selectCateKeys.length !== 3) {
                // 情况级联选择框内容
                this.selectCateKeys = []
                // 提示用户错误信息并返回
                return this.$message.error('请选择三级商品分类!')
            }

            this.getParamsData()
        },
        // tab页签点击事件
        handleTabClick (tab, event) {
            this.getParamsData()
        },
        // 获取参数列表数据
        async getParamsData () {
            //  根据所属分类的id,和当前所属的面板,获取对应的参数
            const res = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: this.activeName } })
            if (res.meta.status !== 200) {
                return this.$message.error('请先选择商品分类!')
            }
            res.data.forEach(item => {
                item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
                // 控制文本框的输入与隐藏
                item.inputVisible = false
                // 文本框的内容
                item.inputValue = ''
            })
            if (this.activeName === 'many') {
                this.manyTableData = res.data
            } else {
                this.onlyTableData = res.data
            }
        },
        // 当添加表单对话框关闭时 清除表单数据和校验信息
        addFormDialogColsed () {
            this.$refs.addFormRef.resetFields()
        },
        // 点击了添加按钮 添加参数
        addParams () {
            // 预校验信息
            this.$refs.addFormRef.validate(async valid => {
                // 如果参数格式不符合要求
                if (!valid) {
                    // 返回 并提示错误信息
                    return this.$message.error('请输入正确的参数信息!')
                }
                // 向服务器发送请求 添加参数信息
                const res = await this.$http.post(`categories/${this.cateId}/attributes`, { attr_name: this.addForm.attr_name, attr_sel: this.activeName })
                // 如果添加参数失败 状态码不为201
                if (res.meta.status !== 201) {
                    // 返回并提示错误信息
                    return this.$message.error('添加参数失败!')
                }
                // 提示成功信息
                this.$message.success('添加参数成功!')
                //  重新获取参数列表数据
                this.getParamsData()
                // 关闭添加对话框
                this.addDialogVisible = false
            })
        },
        // 点击编辑按钮 显示编辑对话框
        async showEditDialog (id) {
            const res = await this.$http.get(`categories/${this.cateId}/attributes/${id}`, { params: { attr_sel: this.activeName } })
            if (res.meta.status !== 200) {
                return this.$message.error('参数信息获取失败!')
            }
            this.editForm = res.data
            this.editDialogVisible = true
        },
        // 当编辑表单对话框关闭时 清除表单数据和校验信息
        editFormDialogColsed () {
            this.$refs.editFormRef.resetFields()
        },
        // 点击了编辑按钮 修改参数
        editParams () {
            // 验证数据格式是否符合要求
            this.$refs.editFormRef.validate(async valid => {
                // 校验数据失败
                if (!valid) {
                    // 返回并提示错误信息
                    return this.$message.error('请输入正确的参数信息!')
                }
                // 向服务器发送请求 修改参数信息
                const res = await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`, { attr_name: this.editForm.attr_name, attr_sel: this.activeName })
                // 如果修改参数信息失败 状态码不为200
                if (res.meta.status !== 200) {
                    // 返回并提示错误信息
                    return this.$message.error('修改参数失败!')
                }
                // 提示成功信息
                this.$message.success('修改参数成功!')
                // 重新获取参数列表数据
                this.getParamsData()
                // 关闭修改对话框
                this.editDialogVisible = false
            })
        },
        // 点击删除按钮 删除参数
        deleteParams (id) {
            // 弹出确认提示框防止用户误触删除按钮
            this.$confirm('此操作将永久删除该参数信息, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {
                // 向服务器发送请求 删除参数信息
                const res = await this.$http.delete(`categories/${this.cateId}/attributes/${id}`)
                if (res.meta.status !== 200) {
                    return this.$message.error('参数信息删除失败！')
                }
                // 重新获取参数列表信息
                this.getParamsData()
                this.$message.success('参数信息删除成功!')
            }).catch(() => {
                this.$message.info('已取消删除!')
            })
        },
        // 文本框失去焦点或者按下enter键
        async handleInputConfirm (row) {
            // 如果用户只输入了空格
            if (row.inputValue.trim().length === 0) {
                // 清空输入框数据
                row.inputValue = ''
                // 隐藏输入框
                row.inputVisible = false
                return
            }
            // 如果输入非空格的内容 继续处理输入的数据
            row.attr_vals.push(row.inputValue.trim())
            // 清空输入框数据
            row.inputValue = ''
            // 隐藏输入框
            row.inputVisible = false
            // 向数据发送请求 编辑提交参数
            this.saveAttrVals(row)
        },
        // 显示文本输入框框
        showInput (row) {
            row.inputVisible = true
            // 当页面元素重新渲染之后,自动调用$nextTick方法
            this.$nextTick(_ => {
                // 让文本框自动获取焦点
                this.$refs.saveTagInput.$refs.input.focus()
            })
        },
        // 将堆attr_vals的操作保存到数据库中
        async saveAttrVals (row) {
            // 向数据发送请求 编辑提交参数
            const res = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
                attr_name: row.attr_name,
                attr_sel: row.attr_sel,
                attr_vals: row.attr_vals.join(' ')
            })
            if (res.meta.status !== 200) {
                return this.$message.error('修改参数信息失败!')
            }
            this.$message.success('修改参数信息成功!')
        },
        // 点击按钮 删除参数
        handleClose (i, row) {
            // 删除attr_vals数组中的对应数据
            row.attr_vals.splice(i, 1)
            // 向数据发送请求 编辑提交参数
            this.saveAttrVals(row)
        }
    },
    computed: {
    // 如果按钮需要被禁用则返回true,否则返回false
        isBtnDisabled () {
            if (this.selectCateKeys.length !== 3) {
                return true
            }
            return false
        },
        // 当前选中的三级分类的id
        cateId () {
            if (this.selectCateKeys.length === 3) {
                return this.selectCateKeys[2]
            }
            return null
        },
        // 动态计算标题的文本
        titleText () {
            if (this.activeName === 'many') {
                return '动态参数'
            }
            return '静态属性'
        }
    }
}
</script>

<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}
.el-tag {
  margin: 6px;
}
.input-new-tag {
  width: 120px;
}
</style>